lity utilities\n// Note: Deprecated .visible-xs, .visible-sm, .visible-md, and .visible-lg as of v3.2.0\n.visible-xs,\n.visible-sm,\n.visible-md,\n.visible-lg {\n\t.responsive-invisibility();\n}\n\n.visible-xs-block,\n.visible-xs-inline,\n.visible-xs-inline-block,\n.visible-sm-block,\n.visible-sm-inline,\n.visible-sm-inline-block,\n.visible-md-block,\n.visible-md-inline,\n.visible-md-inline-block,\n.visible-lg-block,\n.visible-lg-inline,\n.visible-lg-inline-block {\n\tdisplay: none !important;\n}\n\n.visible-xs {\n\t@media (max-width: @screen-xs-max) {\n\t\t.responsive-visibility();\n\t}\n}\n.visible-xs-block {\n\t@media (max-width: @screen-xs-max) {\n\t\tdisplay: block !important;\n\t}\n}\n.visible-xs-inline {\n\t@media (max-width: @screen-xs-max) {\n\t\tdisplay: inline !important;\n\t}\n}\n.visible-xs-inline-block {\n\t@media (max-width: @screen-xs-max) {\n\t\tdisplay: inline-block !important;\n\t}\n}\n\n.visible-sm {\n\t@media (min-width: @screen-sm-min) and (max-width: @screen-sm-max) {\n\t\t.responsive-visibility();\n\t}\n}\n.visible-sm-block {\n\t@media (min-width: @screen-sm-min) and (max-width: @screen-sm-max) {\n\t\tdisplay: block !important;\n\t}\n}\n.visible-sm-inline {\n\t@media (min-width: @screen-sm-min) and (max-width: @screen-sm-max) {\n\t\tdisplay: inline !important;\n\t}\n}\n.visible-sm-inline-block {\n\t@media (min-width: @screen-sm-min) and (max-width: @screen-sm-max) {\n\t\tdisplay: inline-block !important;\n\t}\n}\n\n.visible-md {\n\t@media (min-width: @screen-md-min) and (max-width: @screen-md-max) {\n\t\t.responsive-visibility();\n\t}\n}\n.visible-md-block {\n\t@media (min-width: @screen-md-min) and (max-width: @screen-md-max) {\n\t\tdisplay: block !important;\n\t}\n}\n.visible-md-inline {\n\t@media (min-width: @screen-md-min) and (max-width: @screen-md-max) {\n\t\tdisplay: inline !important;\n\t}\n}\n.visible-md-inline-block {\n\t@media (min-width: @screen-md-min) and (max-width: @screen-md-max) {\n\t\tdisplay: inline-block !important;\n\t}\n}\n\n.visible-lg {\n\t@media (min-width: @screen-lg-min) {\n\t\t.responsive-visibility();\n\t}\n}\n.visible-lg-block {\n\t@media (min-width: @screen-lg-min) {\n\t\tdisplay: block !important;\n\t}\n}\n.visible-lg-inline {\n\t@media (min-width: @screen-lg-min) {\n\t\tdisplay: inline !important;\n\t}\n}\n.visible-lg-inline-block {\n\t@media (min-width: @screen-lg-min) {\n\t\tdisplay: inline-block !important;\n\t}\n}\n\n.hidden-xs {\n\t@media (max-width: @screen-xs-max) {\n\t\t.responsive-invisibility();\n\t}\n}\n.hidden-sm {\n\t@media (min-width: @screen-sm-min) and (max-width: @screen-sm-max) {\n\t\t.responsive-invisibility();\n\t}\n}\n.hidden-md {\n\t@media (min-width: @screen-md-min) and (max-width: @screen-md-max) {\n\t\t.responsive-invisibility();\n\t}\n}\n.hidden-lg {\n\t@media (min-width: @screen-lg-min) {\n\t\t.responsive-invisibility();\n\t}\n}","// Responsive utilities\n\n//\n// More easily include all the states for responsive-utilities.less.\n.responsive-visibility() {\n  display: block !important;\n  table&  { display: table !important; }\n  tr&     { display: table-row !important; }\n  th&,\n  td&     { display: table-cell !important; }\n}\n\n.responsive-invisibility() {\n  display: none !important;\n}","//\n// Scaffolding\n// ==============================\n\n// Inspired by https://github.com/twbs/bootstrap\n\n\n// Reset the box-sizing\n\n* {\n\t.box-sizing(border-box);\n}\n*:before,\n*:after {\n\t.box-sizing(border-box);\n}\n\n\n// Body reset\n\nhtml {\n\tfont-size: @font-size-base; // apply here so we can use REMs\n\t-webkit-tap-highlight-color: rgba(0,0,0,0);\n}\n\nbody {\n\tbackground-color: @body-bg;\n\tcolor: @text-color;\n\tfont-family: @font-family-base;\n\tline-height: @line-height-base;\n}\n\n// Reset fonts for relevant elements\ninput,\nbutton,\nselect,\ntextarea {\n\tfont-family: inherit;\n\tfont-size: inherit;\n\tline-height: inherit;\n}\n\n\n// Links\n\na, .a {\n\tcolor: @link-color;\n\tcursor: pointer;\n\ttext-decoration: none;\n\n\t&:hover,\n\t&:focus {\n\t\tcolor: @link-hover-color;\n\t\ttext-decoration: @link-hover-decoration;\n\t}\n}\n\n\n// Images\n\nimg {\n\tvertical-align: middle;\n}\n\n\n// Horizontal rules\n\nhr {\n\tborder: 0;\n\tborder-top: 1px solid fade(@text-color, 10%);\n\tmargin-bottom: 2em;\n\tmargin-top:    2em;\n}","//\n// Tables\n// ==============================\n\n\n\n// Base\n// ------------------------------\n\ntable {\n\tborder-collapse: collapse;\n}\n.Table {\n\ttext-align: left;\n\twidth: 100%;\n\n\t// alternate alignment (defaults to `middle`)\n\t&.align-bottom td,\n\t&.align-bottom th { vertical-align: bottom; }\n\t&.align-top td,\n\t&.align-top th { vertical-align: top; }\n}\n\n\n\n// Cells\n// ------------------------------\n\n// common\n\n.Table td,\n.Table th {\n\tborder-top: 1px solid @table-border-color;\n\tdisplay: table-cell;\n\tpadding: @table-padding;\n\tvertical-align: middle;\n\n\t// make the content flush\n\t&:first-child {\n\t\tpadding-left: 0;\n\n\t\t// > a {\n\t\t// \tmargin-left: 0;\n\t\t// \tpadding-left: 0;\n\t\t// }\n\t}\n\t&:last-child {\n\t\tpadding-right: 0;\n\n\t\t// > a {\n\t\t// \tmargin-right: 0;\n\t\t// \tpadding-right: 0;\n\t\t// }\n\t}\n}\n\n// links in cells\n\n// .Table td > a {\n// \tcolor: @text-color;\n// \tdisplay: inline-block;\n// \tmargin: -@table-padding;\n// \tpadding: @table-padding;\n//\n// \t&:hover,\n// \t&:focus {\n// \t\tcolor: @text-color;\n// \t\toutline: none;\n// \t}\n// }\n\n\n// header\n\n.Table th {\n\tborder-bottom: 2px solid @table-border-color;\n\tborder-top: 0;\n\tcolor: @gray-light;\n\tdisplay: table-cell;\n\tfont-weight: normal;\n\t// text-transform: uppercase;\n\ttext-align: left;\n\tvertical-align: bottom;\n}\n\n\n// section\n\n.Table-section {\n\tbackground-color: #fafafa;\n\tcolor: @gray;\n\tfont-size: @font-size-sm;\n\tfont-weight: 500;\n\ttext-transform: uppercase;\n}\n\n\n// selected rows\n\n/*tr*/.row-selected > td {\n\tbackground-color: @table-selected-row-bg;\n\tcolor: @table-selected-row-color;\n}\n\n\n\n\n// Sorting\n// ------------------------------\n\n\n// label and clickable area\n\n.th-sort {\n\tcolor: inherit;\n\tdisplay: block;\n\n\t&:hover,\n\t&:focus {\n\t\tcolor: @text-color;\n\t\ttext-decoration: none;\n\n\t\t.th-sort__icon {\n\t\t\topacity: 1;\n\t\t}\n\t}\n}\n\n\n// indicate sort order\n\n.th-sort--asc,\n.th-sort--desc {\n\tcolor: @text-color;\n\n\t.th-sort__icon {\n\t\topacity: 1;\n\t}\n}\n\n\n// combo arrows\n\n.th-sort__icon {\n\t.transition( opacity 150ms linear );\n\t.square( (@table-sort-arrow-width * 4) + 2 ); // fit the arrows with a 2px gap between them\n\tdisplay: inline-block;\n\tmargin-left: @table-sort-arrow-width;\n\topacity: 0;\n\tposition: relative;\n\ttop: 3px;\n\n\t&:before,\n\t&:after {\n\t\t.square(0);\n\t\tborder: @table-sort-arrow-width solid transparent;\n\t\tcontent: \"\";\n\t\tposition: absolute;\n\t}\n\n\t// up arrow\n\t&:before {\n\t\tborder-bottom-color: @text-color;\n\t\ttop: 0;\n\t}\n\n\t// down arrow\n\t&:after {\n\t\tborder-top-color: @text-color;\n\t\tbottom: 0;\n\t}\n\n\t// differentiate between asc/desc\n\t.th-sort--asc &:after {\n\t\topacity: .5;\n\t}\n\t.th-sort--desc &:before {\n\t\topacity: .5;\n\t}\n}\n","// \n// Sizing shortcuts\n// ==============================\n\n// Gratefully taken from https://github.com/twbs/bootstrap\n\n.size(@width; @height) {\n\theight: @height;\n\twidth: @width;\n}\n\n.square(@size) {\n\t.size(@size; @size);\n}\n","//\n// Type\n// ==============================\n\n\n\n\n\n// Headings\n// ------------------------------\n\nh1, h2, h3, h4, h5, h6,\n.h1, .h2, .h3, .h4, .h5, .h6 {\n\tcolor: @heading-color;\n\tfont-weight: 500;\n\tline-height: 1;\n\tmargin-bottom: .66em;\n\tmargin-top: 0;\n}\n\nh1, .h1 {\n\tfont-size: 3em;\n}\nh2, .h2 {\n\tfont-size: 2em;\n\tfont-weight: 300;\n}\nh3, .h3 {\n\t// color: @gray-light;\n\tfont-size: 1.25em;\n\t// text-transform: uppercase;\n}\nh4, .h4 {\n\tfont-size: 1em;\n}\nh5, .h5 {\n\tfont-size: .85em;\n}\nh6, .h6 {\n\tfont-size: .75em;\n}\n\n\n\n// Lead\n// ------------------------------\n\n.lead {\n\tcolor: @gray;\n\tfont-size: @font-size-lg;\n\tfont-weight: 300;\n\tmargin-top: 2em;\n}","//\n// Alerts\n// --------------------------------------------------\n\n\n// Base styles\n// -------------------------\n\n.Alert {\n\tpadding: .75em  1em;\n\tmargin-bottom: 1em;\n\tborder: 1px solid transparent;\n\tborder-radius: @border-radius-base;\n\n\t// Headings for larger alerts\n\th4 {\n\t\tmargin-top: 0;\n\t\t// Specified for the h4 to prevent conflicts of changing @headingsColor\n\t\tcolor: inherit;\n\t}\n\n\t// Improve alignment and spacing of inner content\n\t> p,\n\t> ul {\n\t\tmargin-bottom: 0;\n\t}\n\t> p + p {\n\t\tmargin-top: 5px;\n\t}\n}\n\n// Dismissable alerts\n//\n// Expand the right padding and account for the close button's positioning.\n\n.Alert--dismissable {\n\tpadding-right: 2em;\n\n\t// Adjust close link position\n\t.close {\n\t\tposition: relative;\n\t\ttop: -2px;\n\t\tright: -21px;\n\t\tcolor: inherit;\n\t}\n}\n\n// Alternate styles\n//\n// Generate contextual modifier classes for colorizing the alert.\n\n.Alert--success {\n\t.alert-variant(@app-success);\n}\n.Alert--info {\n\t.alert-variant(@app-info);\n}\n.Alert--warning {\n\t.alert-variant(@app-warning);\n}\n.Alert--danger,\n.Alert--error {\n\t.alert-variant(@app-danger);\n}\n","//\n// Alerts\n// ==============================\n\n.alert-variant(@color) {\n\tbackground-color: fade(@color, 10%);\n\tborder-color: fade(@color, 5%);\n\tcolor: @color;\n\n\thr {\n\t\tborder-top-color: darken(@color, 5%);\n\t}\n\ta {\n\t\tcolor: darken(@color, 10%);\n\t}\n}\n","// \n// Blank State (empty list)\n// ==============================\n\n.BlankState {\n\tbackground-color: @blankstate-bg;\n\tborder-radius: @blankstate-border-radius;\n\tcolor: @blankstate-color;\n\tpadding: @blankstate-padding-vertical @blankstate-padding-horizontal;\n\ttext-align: center;\n}\n\n.BlankState__heading {\n\tcolor: inherit;\n\t\n\t&:last-child {\n\t\tmargin-bottom: 0;\n\t}\n}","//\n// Buttons\n// ==============================\n\n\n\n\n// Base styles\n// ------------------------------\n\n.Button {\n\t.user-select(none);\n\tbackground: none;\n\tborder: 1px solid transparent;\n\tborder-radius: @border-radius-base;\n\tcursor: pointer;\n\tdisplay: inline-block;\n\tfont-weight: @button-font-weight;\n\tline-height: @component-line-height;\n\theight: @component-height;\n\tmargin-bottom: 0;\n\toverflow: hidden; // contain gradient on IE9\n\tpadding: 0 @button-padding-horizontal;\n\ttext-align: center;\n\ttouch-action: manipulation;\n\tvertical-align: middle;\n\twhite-space: nowrap;\n\t-webkit-appearance: none;\n\n\t&:hover,\n\t&:focus,\n\t&.focus,\n\t&.is-focus {\n\t\tcolor: @button-default-color;\n\t\ttext-decoration: none;\n\t}\n\n\t&:active,\n\t&.active,\n\t&.is-active {\n\t\tbackground-image: none;\n\t\toutline: 0;\n\t}\n\n\t&.disabled,\n\t&[disabled] {\n\t\topacity: .4;\n\t\tpointer-events: none;\n\t}\n}\n\n\n\n\n// Button Variants\n// ------------------------------\n\n\n// special case for the default button\n\n.Button--default {\n\t#gradient .vertical(#fafafa, #eaeaea);\n\tborder: 1px solid @input-border-color;\n\tborder-color: @input-border-color darken(@input-border-color, 6%) darken(@input-border-color, 12%);\n\tcolor: @text-color;\n\ttext-shadow: 0 1px 0 white;\n\n\n\t&:hover {\n\t\t#gradient .vertical(#fff, #eee);\n\t\tborder-color: darken(@input-border-color, 5%) darken(@input-border-color, 5%) darken(@input-border-color, 10%);\n\t\tbox-shadow: 0 1px 0 rgba(0,0,0,0.1);\n\t\tcolor: @text-color;\n\t}\n\t&:focus {\n\t\tborder-color: @app-primary;\n\t\tbox-shadow: 0 0 0 3px fade(@app-primary, 10%);\n\t\tcolor: @text-color;\n\t\toutline: none;\n\t}\n\t&.is-active,\n\t&:active {\n\t\tbackground: #e6e6e6;\n\t\tborder-color: darken(@input-border-color, 10%);\n\t\tbox-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);\n\t\tcolor: @text-color;\n\t}\n\n\t// yuck...\n\t&.is-active:focus:not(:active) {\n\t\tborder-color: @app-primary;\n\t\tbox-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1), 0 0 0 3px fade(@app-primary, 10%);\n\t}\n\n\t&.disabled,\n\t&[disabled] {\n\t\tbackground-color: @button-default-disabled-bg;\n\t}\n}\n\n// default styles\n\n.Button--default-primary {\n\t.button-default-variant(@button-primary-color);\n}\n.Button--default-success {\n\t.button-default-variant(@button-success-color);\n}\n.Button--default-warning {\n\t.button-default-variant(@button-warning-color);\n}\n.Button--default-danger {\n\t.button-default-variant(@button-danger-color);\n}\n\n\n// fill styles\n\n.Button--primary {\n\t.button-variant(white; @button-primary-bg);\n}\n.Button--success {\n\t.button-variant(white; @button-success-bg);\n}\n.Button--warning {\n\t.button-variant(white; @button-warning-bg);\n}\n.Button--danger {\n\t.button-variant(white; @button-danger-bg);\n}\n\n\n// hollow styles\n\n.Button--hollow-primary {\n\t.button-hollow-variant(@button-primary-color; @button-primary-border);\n}\n.Button--hollow-success {\n\t.button-hollow-variant(@button-success-color; @button-success-border);\n}\n.Button--hollow-warning {\n\t.button-hollow-variant(@button-warning-color; @button-warning-border);\n}\n.Button--hollow-danger {\n\t.button-hollow-variant(@button-danger-color; @button-danger-border);\n}\n\n\n\n\n// Link buttons\n// -------------------------\n\n// Make a button look and behave like a link\n.Button--link {\n\t.button-link-variant(@link-color; @link-hover-color; @link-hover-decoration);\n}\n\n// Make a button look and behave like text.\n.Button--link-text {\n\t.button-link-variant(@text-color; @app-primary; @link-hover-decoration);\n}\n\n.Button--link-primary {\n\t.button-link-variant(@app-primary; @app-primary; @link-hover-decoration);\n}\n.Button--link-success {\n\t.button-link-variant(@app-success; @app-success; @link-hover-decoration);\n}\n.Button--link-warning {\n\t.button-link-variant(@app-warning; @app-warning; @link-hover-decoration);\n}\n.Button--link-danger {\n\t.button-link-variant(@app-danger; @app-danger; @link-hover-decoration);\n}\n\n// Some special link button styles.\n.Button--link-cancel {\n\t.button-link-variant(@gray-light; @app-danger; @link-hover-decoration);\n}\n.Button--link-delete {\n\t.button-link-variant(@gray-light; @app-danger; @link-hover-decoration);\n\t&:hover,\n\t&:focus {\n\t\t#gradient .vertical(lighten(@app-danger, 4%), darken(@app-danger, 4%));\n\t\tbackground-color: @app-danger;\n\t\tborder-color: darken(@app-danger, 4%) darken(@app-danger, 8%) darken(@app-danger, 12%);\n\t\tbox-shadow: 0 1px 0 rgba(0,0,0,0.1);\n\t\tcolor: white;\n\t\ttext-decoration: none;\n\t}\n\t&:focus {\n\t\tbox-shadow: 0 0 0 3px fade(@app-danger, 25%);\n\t\toutline: none;\n\t}\n\t&:active {\n\t\tbackground-color: darken(@app-danger, 4%);\n\t\tbackground-image: none !important;\n\t\tborder-color: darken(@app-danger, 12%) darken(@app-danger, 8%) darken(@app-danger, 8%);\n\t\tbox-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);\n\t\tcolor: white;\n\t}\n}\n\n\n\n\n// Button Sizes\n// ------------------------------\n\n.Button--lg { font-size: @font-size-lg; }\n.Button--sm { font-size: @font-size-sm; }\n.Button--xs {\n\tfont-size: @font-size-xs;\n\tline-height: 1.9;\n\tpadding-left: .66em;\n\tpadding-right: .66em;\n}\n\n\n\n\n// Block button\n// ------------------------------\n\n.Button--block {\n\tdisplay: block;\n\tpadding-left: 0;\n\tpadding-right: 0;\n\twidth: 100%;\n}\n\n\n\n\n// Button Goups\n// ------------------------------\n\n.ButtonGroup {\n\tdisplay: inline-block;\n\tposition: relative;\n\tvertical-align: middle;\n\n\t> .Button {\n\t\tborder-radius: 0;\n\t\tfloat: left;\n\t\tmargin-left: -1px;\n\n\t\t&:first-child {\n\t\t\t.border-left-radius(@border-radius-base);\n\t\t\tmargin-left: 0;\n\t\t}\n\t\t&:last-child {\n\t\t\t.border-right-radius(@border-radius-base);\n\t\t}\n\t\t&:hover,\n\t\t&:active,\n\t\t&:focus {\n\t\t\tposition: relative;\n\t\t}\n\t\t// circumvent bug where adjacent siblings would overlap on hover\n\t\t&:focus {\n\t\t\tz-index: 1;\n\t\t}\n\t}\n}\n\n\n\n\n// Handle icons inside buttons\n// ------------------------------\n\n.Button > .octicon {\n\t&:first-child { margin-right: 0.5em; }\n\t&:last-child  { margin-left:  0.5em; }\n\t&:only-child {\n\t\tmargin-left: 0;\n\t\tmargin-right: 0;\n\t}\n}\n","// \n// Gradients\n// ==============================\n\n// Gratefully taken from https://github.com/twbs/bootstrap\n\n#gradient {\n\n\t// Horizontal gradient, from left to right\n\t//\n\t// Creates two color stops, start and end, by specifying a color and position for each color stop.\n\t// Color stops are not available in IE9 and below.\n\t.horizontal(@start-color: #555; @end-color: #333; @start-percent: 0%; @end-percent: 100%) {\n\t\tbackground-image: -webkit-linear-gradient(left, @start-color @start-percent, @end-color @end-percent); // Safari 5.1-6, Chrome 10+\n\t\tbackground-image: -o-linear-gradient(left, @start-color @start-percent, @end-color @end-percent); // Opera 12\n\t\tbackground-image: linear-gradient(to right, @start-color @start-percent, @end-color @end-percent); // Standard, IE10, Firefox 16+, Opera 12.10+, Safari 7+, Chrome 26+\n\t\tbackground-repeat: repeat-x;\n\t\tfilter: e(%(\"progid:DXImageTransform.Microsoft.gradient(startColorstr='%d', endColorstr='%d', GradientType=1)\",argb(@start-color),argb(@end-color))); // IE9 and down\n\t}\n\n\t// Vertical gradient, from top to bottom\n\t//\n\t// Creates two color stops, start and end, by specifying a color and position for each color stop.\n\t// Color stops are not available in IE9 and below.\n\t.vertical(@start-color: #555; @end-color: #333; @start-percent: 0%; @end-percent: 100%) {\n\t\tbackground-image: -webkit-linear-gradient(top, @start-color @start-percent, @end-color @end-percent);  // Safari 5.1-6, Chrome 10+\n\t\tbackground-image: -o-linear-gradient(top, @start-color @start-percent, @end-color @end-percent);  // Opera 12\n\t\tbackground-image: linear-gradient(to bottom, @start-color @start-percent, @end-color @end-percent); // Standard, IE10, Firefox 16+, Opera 12.10+, Safari 7+, Chrome 26+\n\t\tbackground-repeat: repeat-x;\n\t\tfilter: e(%(\"progid:DXImageTransform.Microsoft.gradient(startColorstr='%d', endColorstr='%d', GradientType=0)\",argb(@start-color),argb(@end-color))); // IE9 and down\n\t}\n\n\t.directional(@start-color: #555; @end-color: #333; @deg: 45deg) {\n\t\tbackground-repeat: repeat-x;\n\t\tbackground-image: -webkit-linear-gradient(@deg, @start-color, @end-color); // Safari 5.1-6, Chrome 10+\n\t\tbackground-image: -o-linear-gradient(@deg, @start-color, @end-color); // Opera 12\n\t\tbackground-image: linear-gradient(@deg, @start-color, @end-color); // Standard, IE10, Firefox 16+, Opera 12.10+, Safari 7+, Chrome 26+\n\t}\n\t.horizontal-three-colors(@start-color: #00b3ee; @mid-color: #7a43b6; @color-stop: 50%; @end-color: #c3325f) {\n\t\tbackground-image: -webkit-linear-gradient(left, @start-color, @mid-color @color-stop, @end-color);\n\t\tbackground-image: -o-linear-gradient(left, @start-color, @mid-color @color-stop, @end-color);\n\t\tbackground-image: linear-gradient(to right, @start-color, @mid-color @color-stop, @end-color);\n\t\tbackground-repeat: no-repeat;\n\t\tfilter: e(%(\"progid:DXImageTransform.Microsoft.gradient(startColorstr='%d', endColorstr='%d', GradientType=1)\",argb(@start-color),argb(@end-color))); // IE9 and down, gets no color-stop at all for proper fallback\n\t}\n\t.vertical-three-colors(@start-color: #00b3ee; @mid-color: #7a43b6; @color-stop: 50%; @end-color: #c3325f) {\n\t\tbackground-image: -webkit-linear-gradient(@start-color, @mid-color @color-stop, @end-color);\n\t\tbackground-image: -o-linear-gradient(@start-color, @mid-color @color-stop, @end-color);\n\t\tbackground-image: linear-gradient(@start-color, @mid-color @color-stop, @end-color);\n\t\tbackground-repeat: no-repeat;\n\t\tfilter: e(%(\"progid:DXImageTransform.Microsoft.gradient(startColorstr='%d', endColorstr='%d', GradientType=0)\",argb(@start-color),argb(@end-color))); // IE9 and down, gets no color-stop at all for proper fallback\n\t}\n\t.radial(@inner-color: #555; @outer-color: #333) {\n\t\tbackground-image: -webkit-radial-gradient(circle, @inner-color, @outer-color);\n\t\tbackground-image: radial-gradient(circle, @inner-color, @outer-color);\n\t\tbackground-repeat: no-repeat;\n\t}\n\t.striped(@color: rgba(255,255,255,.15); @angle: 45deg) {\n\t\tbackground-image: -webkit-linear-gradient(@angle, @color 25%, transparent 25%, transparent 50%, @color 50%, @color 75%, transparent 75%, transparent);\n\t\tbackground-image: -o-linear-gradient(@angle, @color 25%, transparent 25%, transparent 50%, @color 50%, @color 75%, transparent 75%, transparent);\n\t\tbackground-image: linear-gradient(@angle, @color 25%, transparent 25%, transparent 50%, @color 50%, @color 75%, transparent 75%, transparent);\n\t}\n}\n","//\n// Button variants\n// ==============================\n\n// Inspired by https://github.com/twbs/bootstrap\n\n\n// Fill\n\n.button-variant(@color; @background) {\n\t#gradient .vertical(lighten(@background, 5%), darken(@background, 5%));\n\tbackground-color: @background;\n\tborder-color: darken(@background, 5%) darken(@background, 10%) darken(@background, 15%);\n\tbox-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.1);\n\tcolor: @color;\n\tfont-weight: 400;\n\ttext-shadow: 0 -1px 0 rgba(0, 0, 0, 0.25);\n\n\t&:hover,\n\t&:focus,\n\t&.focus {\n\t\t#gradient .vertical(lighten(@background, 10%), @background);\n\t\tborder-color: @background darken(@background, 5%) darken(@background, 10%);\n\t\tbox-shadow: 0 1px 0 rgba(0,0,0,0.1);\n\t\tcolor: @color;\n\t\toutline: none;\n\t}\n\t&:focus,\n\t&.focus {\n\t\tbox-shadow: 0 0 0 3px fade(@background, 25%);\n\t}\n\t&:active,\n\t&.active {\n\t\tbackground-color: darken(@background, 4%);\n\t\tbackground-image: none;\n\t\tborder-color: darken(@background, 15%) darken(@background, 10%) darken(@background, 5%);\n\t\tbox-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);\n\t}\n}\n\n\n// Default\n\n.button-default-variant(@color) {\n\t#gradient .vertical(#fafafa, #eaeaea);\n\tborder: 1px solid @input-border-color;\n\tborder-color: @input-border-color darken(@input-border-color, 6%) darken(@input-border-color, 12%);\n\tcolor: @color;\n\n\n\t&:hover {\n\t\t#gradient .vertical(lighten(@color, 4%), darken(@color, 4%));\n\t\tborder-color: darken(@color, 6%) darken(@color, 12%) darken(@color, 18%);\n\t\tbox-shadow: 0 1px 0 rgba(0,0,0,0.1);\n\t\tcolor: white;\n\t}\n\t&:focus {\n\t\tborder-color: darken(@color, 6%) darken(@color, 12%) darken(@color, 18%);\n\t\tbox-shadow: 0 0 0 3px fade(@color, 25%);\n\t\tcolor: white;\n\t\tcolor: @color;\n\t\toutline: none;\n\t}\n\t&:hover:focus {\n\t\tcolor: white;\n\t}\n\t&:active {\n\t\tbackground-color: darken(@color, 4%);\n\t\tbackground-image: none;\n\t\tborder-color: darken(@color, 18%) darken(@color, 12%) darken(@color, 6%);\n\t\tbox-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);\n\t\tcolor: white;\n\t}\n\n\t&.disabled,\n\t&[disabled] {\n\t\tbackground-color: @button-default-disabled-bg;\n\t}\n}\n\n\n// Hollow\n\n.button-hollow-variant(@color; @border) {\n\tbackground: none;\n\tborder-color: @border;\n\tcolor: @color;\n\n\t&:hover,\n\t&:focus {\n\t\tbackground-color: mix(@border, @body-bg, 8%);\n\t\tbackground-image: none;\n\t\tborder-color: darken(@border, 10%);\n\t\tcolor: @color;\n\t\toutline: none;\n\t}\n\t&:focus,\n\t&.focus {\n\t\tbox-shadow: 0 0 0 3px fade(@border, 10%);\n\t}\n\t&:active {\n\t\tbackground-color: fade(@border, 20%);\n\t\tborder-color: darken(@border, 20%);\n\t\tbox-shadow: none;\n\t}\n}\n\n\n\n// Link\n\n.button-link-variant(@color; @hover-color; @hover-decoration) {\n\tcolor: @color;\n\tfont-weight: normal;\n\n\t&,\n\t&:active,\n\t&.active,\n\t&[disabled],\n\tfieldset[disabled] & {\n\t\t.box-shadow(none);\n\t\tbackground-color: transparent;\n\t}\n\t&,\n\t&:hover,\n\t&:focus,\n\t&:active {\n\t\tborder-color: transparent;\n\t\toutline: none;\n\t}\n\t&:hover,\n\t&:focus {\n\t\tbackground-color: transparent;\n\t\tcolor: @hover-color;\n\t\ttext-decoration: @hover-decoration;\n\t}\n}\n","// \n// Single side border-radius\n// ==============================\n\n// Gratefully taken from https://github.com/twbs/bootstrap\n\n.border-top-radius(@radius) {\n\tborder-top-right-radius: @radius;\n\t border-top-left-radius: @radius;\n}\n.border-right-radius(@radius) {\n\tborder-bottom-right-radius: @radius;\n\t   border-top-right-radius: @radius;\n}\n.border-bottom-radius(@radius) {\n\tborder-bottom-right-radius: @radius;\n\t border-bottom-left-radius: @radius;\n}\n.border-left-radius(@radius) {\n\tborder-bottom-left-radius: @radius;\n\t   border-top-left-radius: @radius;\n}\n","//\n// Card\n// ==============================\n\n.Card {\n  background-color: @card-bg-color;\n  border-radius: @border-radius-sm;\n  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.075), 0 0 0 1px rgba(0,0,0,0.1);\n  margin-bottom: @spacing-md;\n  padding: @spacing-md;\n}\n","//\n// Dropdowns\n// ==============================\n\n// Inspired by https://github.com/twbs/bootstrap\n\n\n\n\n// Wrapper\n// ------------------------------\n\n.Dropdown {\n\tdisplay: inline-block;\n\tposition: relative;\n}\n\n\n\n\n// Menu\n// ------------------------------\n\n\n// base\n\n.Dropdown-menu {\n\t.animation-duration( 100ms );\n\t.animation-timing-function( cubic-bezier(0.77, 0, 0.175, 1) );\n\tbackground-color: @dropdown-bg;\n\tborder-radius: @border-radius-base;\n\tbox-shadow: 0 0 0 1px rgba(0,0,0,.175), 0 3px 8px rgba(0,0,0,.175);\n\tfont-size: @font-size-base;\n\tleft: 0;\n\tlist-style: none;\n\tmargin: 4px 0 0; // override default ul\n\tmin-width: 160px;\n\tpadding: 5px 0;\n\tposition: absolute;\n\ttext-align: left; // ensures proper alignment if parent has alternative alignment\n\ttop: 100%;\n\tz-index: @zindex-dropdown;\n\n\t// fix height and scroll\n\tmax-height: @dropdown-menu-height;\n\toverflow-y: scroll;\n\t-webkit-overflow-scrolling: touch;\n\n\t.align-right & {\n\t\tleft: auto;\n\t\tright: 0;\n\t}\n}\n.Dropdown-menu-enter {\n\t.animation-name( dropdownMenuEnter );\n}\n.Dropdown-menu-leave {\n\t.animation-name( dropdownMenuLeave );\n}\n\n\n// common\n\n.Dropdown-menu__item,\n.Dropdown-menu__header {\n\twhite-space: nowrap;\n}\n\n\n// list item \n\n.Dropdown-menu__action {\n\tclear: both;\n\tcolor: @dropdown-link-color;\n\tcursor: pointer;\n\tdisplay: block;\n\tfont-weight: normal;\n\tline-height: @line-height-base;\n\tpadding: 3px 20px;\n\t\n\t&:hover,\n\t&:focus {\n\t\tbackground-color: @dropdown-link-hover-bg;\n\t\tcolor: @dropdown-link-hover-color;\n\t\ttext-decoration: none;\n\t}\n\n\t// active state\n\t&.active {\n\t\t&,\n\t\t&:hover,\n\t\t&:focus {\n\t\t\tbackground-color: @dropdown-link-active-bg;\n\t\t\tcolor: @dropdown-link-active-color;\n\t\t\toutline: 0;\n\t\t\ttext-decoration: none;\n\t\t}\n\t}\n}\n\n\n// dividers\n\n.Dropdown-menu__divider {\n\tbackground-color: @dropdown-divider-bg;\n\theight: 1px;\n\tmargin-bottom: .25em;\n\tmargin-top: .25em;\n\toverflow: hidden;\n}\n\n\n// headers\n\n.Dropdown-menu__header {\n\tcolor: @dropdown-header-color;\n\tdisplay: block;\n\tfont-size: @font-size-sm;\n\tfont-weight: 500;\n\tline-height: @line-height-base;\n\tmargin-top: 1em;\n\tpadding: 3px 20px;\n\twhite-space: nowrap;\n\n\t// margin looks clumsy when preceeded by a divider\n\t.Dropdown-menu__divider + & {\n\t\tmargin-top: 0;\n\t}\n}\n\n\n// background\n\n.blockout, // catchall\n.Dropdown-menu-backdrop {\n\theight: 100%;\n\tleft: 0;\n\tposition: fixed;\n\ttop: 0;\n\twidth: 100%;\n\tz-index: @zindex-dropdown-backdrop;\n}\n\n\n\n\n// Animation\n// --------------------\n\n@-webkit-keyframes dropdownMenuEnter {\n\tfrom { opacity: 0; -webkit-transform: translate3d(0, -5px, 0); }\n\tto   { opacity: 1; -webkit-transform: translate3d(0, 0, 0); }\n}\n@keyframes dropdownMenuEnter {\n\tfrom { opacity: 0; transform: translate3d(0, -5px, 0); }\n\tto   { opacity: 1; transform: translate3d(0, 0, 0); }\n}\n\n@-webkit-keyframes dropdownMenuLeave {\n\tfrom { opacity: 1; -webkit-transform: translate3d(0, 0, 0); }\n\tto   { opacity: 0; -webkit-transform: translate3d(0, -5px, 0); }\n}\n@keyframes dropdownMenuLeave {\n\tfrom { opacity: 1; transform: translate3d(0, 0, 0); }\n\tto   { opacity: 0; transform: translate3d(0, -5px, 0); }\n}\n\n\n\n\n\n\n\n\n\n","//\n// File Drag and Drop\n// ==============================\n\n\n.FileDragAndDrop {\n\t.transition( border 120ms );\n\tbackground: none;\n\tborder: 2px dashed @input-border-color;\n\tborder-radius: @border-radius-lg;\n\tcolor: darken(@input-border-color, 20%);\n\tcursor: pointer;\n\theight: 120px;\n\tline-height: 120px;\n\tpadding: 0 1em;\n\ttext-align: center;\n\twidth: 100%;\n\n\t&:focus,\n\t&.active {\n\t\tborder-color: lighten(@input-border-color-focus, 20%);\n\t\tborder-style: solid;\n\t\tcolor: @input-border-color-focus;\n\t\toutline: none;\n\t\t-webkit-appearance: none;\n\t}\n}\n.FileDragAndDrop__label {\n\tdisplay: inline-block;\n\tfont-weight: 500;\n\tline-height: 1.2;\n\tvertical-align: middle;\n}","//\n// File Upload\n// ==============================\n\n\n.FileUpload {\n\toverflow: hidden;\n}\n\n.FileUpload__image {\n\tborder-radius: @border-radius-base;\n\tborder: 1px solid @input-border-color;\n\tfloat: left;\n\tmargin-right: @grid-gutter-width;\n\tpadding: 5px;\n\twidth: 120px;\n}\n.FileUpload__image-src {\n\theight: auto;\n\tmax-width: 100%;\n}\n.FileUpload__message {\n\t.text-overflow();\n\tcolor: @gray;\n\tdisplay: inline-block;\n\tmargin-bottom: @spacing-md;\n}\n.FileUpload__content {\n\t&:extend(.u-clearfix all);\n}","// \n// Text overflow\n// ------------------------------\n\n// Gratefully taken from https://github.com/twbs/bootstrap\n\n.text-overflow(@display: block) {\n\tdisplay: @display;\n\toverflow: hidden;\n\ttext-overflow: ellipsis;\n\twhite-space: nowrap;\n}\n","//\n// Form Input\n// ==============================\n\n\n// Base\n// ------------------------------\n\n.FormInput {\n\t.transition( ~\"border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s\" );\n\tbackground-color: @input-bg-color;\n\tbackground-image: none;\n\tborder-radius: @border-radius-base;\n\tborder: 1px solid @input-border-color;\n\tborder-top-color: darken(@input-border-color, 4%);\n\tborder-bottom-color: lighten(@input-border-color, 4%);\n\tbox-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);\n\tcolor: @text-color;\n\tdisplay: block;\n\t// font-size: @font-size-base;\n\tline-height: 1.2;\n\theight: @component-height;\n\tpadding: 0 @input-padding-horizontal;\n\twidth: 100%;\n\t-webkit-appearance: none;\n\t-moz-appearance: none;\n\n\n\t// states\n\n\t&:hover {\n\t\tborder-color: @input-border-color-hover;\n\t\toutline: 0;\n\t}\n\t&.focus,\n\t&.is-focused,\n\t&:focus {\n\t\tborder-color: @input-border-color-focus;\n\t\tbox-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 0 3px fade(@input-border-color-focus, 10%);\n\t\t// box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 4px fade(@input-border-color-focus, 33%);\n\t\toutline: 0;\n\t}\n\t&.disabled,\n\t&[disabled] {\n\t\tbackground-color: @input-bg-disabled;\n\t\tpointer-events: none;\n\t}\n\n\t// firefox weirdness\n\t&::-moz-focus-inner {\n\t\tborder: 0;\n\t\toutline : 0;\n\t}\n}\n\n\n\n// Sizes\n\n.FormInput--lg { font-size: @font-size-lg; }\n.FormInput--sm { font-size: @font-size-sm; }\n.FormInput--xs {\n\tfont-size: @font-size-xs;\n\tline-height: 1.9;\n\tpadding-left: .66em;\n\tpadding-right: .66em;\n}\n\n\n\n\n// No Edit\n// ------------------------------\n\n// Use an exclusive class, not a modifier as they are distinctly different\n\n/*div*/.FormInput-noedit {\n\t.u-text-truncate();\n\tbackground-color: darken(@body-bg, 2%);\n\tborder-radius: @border-radius-base;\n\tborder: 1px solid darken(@body-bg, 8%);\n\tcolor: @text-color;\n\tdisplay: inline-block;\n\tfont-size: @font-size-base;\n\tline-height: @component-line-height;\n\theight: @component-height;\n\tmin-width: @input-noedit-width; // still unsure about this\n\tpadding: 0 @input-padding-horizontal;\n\tvertical-align: middle;\n\twidth: auto;\n\n\t// indicate clickability when using an anchor\n\ta& {\n\t\tbackground-color: fade(@link-color, 5%);\n\t\tborder-color: fade(@link-color, 10%);\n\t\tcolor: @link-color;\n\t\tmargin-right: 5px;\n\t\tmin-width: 0;\n\t\ttext-decoration: none;\n\n\t\t&:hover,\n\t\t&:focus {\n\t\t\tbackground-color: fade(@link-color, 10%);\n\t\t\tborder-color: fade(@link-color, 10%);\n\t\t\tcolor: @link-color;\n\t\t\toutline: none;\n\t\t\ttext-decoration: underline;\n\t\t};\n\t}\n}\n.FormInput-noedit--multiline {\n\tline-height: 1.3;\n\theight: auto;\n\tpadding: .5em @input-padding-horizontal;\n\twhite-space: normal;\n}\n\n// not sure why this was here. remove when confirm/deny\n// @media (min-width: @screen-sm) {\n// \t.FormInput-noedit {\n// \t\tdisplay: block;\n// \t}\n// }\n\n\n\n\n// Textarea\n// ------------------------------\n\ntextarea.FormInput {\n\t.resizable(vertical);\n\theight: auto;\n\tline-height: 1.4em;\n\tmin-height: 6.75em; // give user's comfortable room to write - about 4 rows\n\tpadding: .5em @input-padding-horizontal;\n}\n\n\n\n\n// Select\n// ------------------------------\n\n\n// base\n\n.FormSelect {\n\tbox-shadow: 0 1px 1px rgba(0, 0, 0, 0.075);\n\tborder-bottom-color: darken(@input-border-color, 4%);\n\tborder-top-color: lighten(@input-border-color, 4%);\n\theight: @component-height;\n\n\t&:focus {\n\t\tbox-shadow: 0 0 0 3px fade(@input-border-color-focus, 10%);\n\t}\n\t&.disabled,\n\t&[disabled] {\n\t\tcolor: @gray-light;\n\t}\n}\n\n\n// arrows\n\n.FormSelect__arrows {\n\tbottom: 0;\n\tline-height: @component-height;\n\tpointer-events: none;\n\tposition: absolute;\n\tright: 0;\n\ttext-align: center;\n\twidth: @component-height;\n\n\t& > svg {\n\t\tfill: @text-color;\n\t}\n}\n.FormSelect__arrows--disabled > svg {\n\tfill: @gray-light;\n}\n","/**\n * Word breaking\n *\n * Break strings when their length exceeds the width of their container.\n */\n\n.u-text-break {\n\tword-wrap: break-word !important;\n}\n\n/**\n * Horizontal text alignment\n */\n\n.u-text-center {\n\ttext-align: center !important;\n}\n\n.u-text-left {\n\ttext-align: left !important;\n}\n\n.u-text-right {\n\ttext-align: right !important;\n}\n\n/**\n * Assign basic colours\n */\n\n.u-text-muted {\n\tcolor: @gray-light !important;\n}\n\n.u-text-default {\n\tcolor: @text-color !important;\n}\n\n.u-text-primary {\n\tcolor: @app-primary !important;\n}\n\n.u-text-info {\n\tcolor: @app-info !important;\n}\n\n.u-text-warning {\n\tcolor: @app-warning !important;\n}\n\n.u-text-success {\n\tcolor: @app-success !important;\n}\n\n.u-text-danger {\n\tcolor: @app-danger !important;\n}\n\n/**\n * Inherit the ancestor's text color.\n */\n\n.u-text-inherit-color {\n\tcolor: inherit !important;\n}\n\n/**\n * Capitalize the text\n */\n\n.u-text-caps {\n\ttext-transform: uppercase !important;\n}\n\n/**\n * Enables font kerning in all browsers.\n * http://blog.typekit.com/2014/02/05/kerning-on-the-web/\n *\n * 1. Chrome (not Windows), Firefox, Safari 6+, iOS, Android\n * 2. Chrome (not Windows), Firefox, IE 10+\n * 3. Safari 7 and future browsers\n */\n\n.u-text-kern {\n\ttext-rendering: optimizeLegibility; /* 1 */\n\tfont-feature-settings: \"kern\" 1; /* 2 */\n\tfont-kerning: normal; /* 3 */\n}\n\n/**\n * Prevent whitespace wrapping\n */\n\n.u-text-no-wrap {\n\twhite-space: nowrap !important;\n}\n\n/**\n * Text truncation\n *\n * Prevent text from wrapping onto multiple lines, and truncate with an\n * ellipsis.\n *\n * 1. Ensure that the node has a maximum width after which truncation can\n *    occur.\n * 2. Fix for IE 8/9 if `word-wrap: break-word` is in effect on ancestor\n *    nodes.\n */\n\n.u-text-truncate {\n\tmax-width: 100%; /* 1 */\n\toverflow: hidden;\n\ttext-overflow: ellipsis;\n\twhite-space: nowrap;\n\tword-wrap: normal; /* 2 */\n}\n","// \n// Resize anything\n// ------------------------------\n\n// Gratefully taken from https://github.com/twbs/bootstrap\n\n.resizable(@direction) {\n\toverflow: auto;\n\tresize: @direction; // Options: horizontal, vertical, both\n}\n","//\n// Form Note\n// ==============================\n\n\n\n\n// Base\n// ------------------------------\n\n.FormNote {\n\tfont-size: @font-size-sm;\n\tmargin-bottom: @spacing-sm;\n\tmargin-top: @spacing-sm;\n}\n\n\n\n\n// Variants\n// ------------------------------\n\n.FormNote--default { color: @form-help-color; }\n.FormNote--primary { color: @app-primary; }\n.FormNote--success { color: @app-success; }\n.FormNote--info    { color: @app-info; }\n.FormNote--warning { color: @app-warning; }\n.FormNote--danger  { color: @app-danger; }","// \n// Icons\n// ==============================\n\n\n\n// wrapper\n\n.IconField {\n\tposition: relative;\n\n\t&.left {\n\t\t> .FormInput { padding-left: (@input-padding-horizontal * 3); }\n\t\t> .IconField__icon {\n\t\t\t.border-left-radius(@border-radius-base);\n\t\t\tleft: 0;\n\t\t}\n\t\t> .Spinner {\n\t\t\tleft: 8px;\n\t\t}\n\t\t&.has-fill-icon > .FormInput { padding-left: (@input-padding-horizontal * 2) + 1.5; }\n\t}\n\t&.right {\n\t\t> .FormInput { padding-right: (@input-padding-horizontal * 3); }\n\t\t> .IconField__icon {\n\t\t\t.border-right-radius(@border-radius-base);\n\t\t\tright: 0;\n\t\t}\n\t\t> .Spinner {\n\t\t\tright: 8px;\n\t\t}\n\t\t&.has-fill-icon > .FormInput { padding-right: (@input-padding-horizontal * 2) + 1.5; }\n\t}\n}\n\n\n// icon\n\n.IconField__icon {\n\tbottom: 0;\n\tpadding-left: .6em;\n\tpadding-right: .6em;\n\tpointer-events: none;\n\tposition: absolute;\n\ttop: 0;\n\twidth: 2.2em;\n\n\t&::before {\n\t\t.square(1em);\n\t\tbackground-position: left top;\n\t\tbackground-repeat: no-repeat;\n\t\tbackground-size: contain;\n\t\tdisplay: block;\n\t\tmargin-top: -0.5em;\n\t\tposition: absolute;\n\t\ttext-align: center;\n\t\ttop: 50%;\n\t}\n}\n\n\n// loading spinner\n\n.IconField > .Spinner {\n\tmargin-top: -2px;\n\tpointer-events: none;\n\tposition: absolute;\n\ttop: 50%;\n}\n\n\n// colour variations\n\n.IconField__icon-color--default { color: #aaa; }\n.IconField__icon-color--danger  { color: @app-danger; }\n.IconField__icon-color--primary { color: @app-primary; }\n.IconField__icon-color--success { color: @app-success; }\n.IconField__icon-color--warning { color: @app-warning; }\n\n.IconField__icon-fill--danger  { background-color: @app-danger;  box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.1); color: white; }\n.IconField__icon-fill--primary { background-color: @app-primary; box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.1); color: white; }\n.IconField__icon-fill--success { background-color: @app-success; box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.1); color: white; }\n.IconField__icon-fill--warning { background-color: @app-warning; box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.1); color: white; }\n\n\n// default is a special case\n\n.IconField__icon-fill--default {\n\tbackground-color: @input-border-color;\n\tbox-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.1);\n\tcolor: @gray;\n\n\t.FormInput:focus + & {\n\t\tbox-shadow: inset -1px 0 0 darken(@input-border-color, 10%), inset 0 0 0 1px @input-border-color-focus;\n\t}\n}\n\n\n// handle focus states on differing field contexts\n\n.field-context-danger  > .FormInput { .field-focus-state(@app-danger); }\n.field-context-success > .FormInput { .field-focus-state(@app-success); }\n.field-context-warning > .FormInput { .field-focus-state(@app-warning); }\n\n\n\n\n\n\n\n\n\n","// \n// Field variants\n// ==============================\n\n.field-focus-state(@color) {\n\t&:focus,\n\t&.focus {\n\t\tborder-color: @color;\n\t\tbox-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 0 3px fade(@color, 10%);\n\t}\n}","//\n// Input Group\n// ==============================\n\n\n// wrapper\n\n.InputGroup {\n\t.display-flex();\n\tmargin-bottom: @spacing-md;\n\n\t// remove the bottom margin when inside a .FormField\n\t// this occurs when using in a .horizontal-form\n\t.FormField & {\n\t\tmargin-bottom: 0;\n\t}\n}\n\n\n// common\n\n.InputGroup_section {\n\n\t// add gutter to additional elements\n\t& + & {\n\t\tpadding-left: @input-padding-horizontal;\n\t}\n\n\t// make flush when contiguous\n\t.InputGroup--contiguous & {\n\t\tpadding-left: 0;\n\t}\n}\n\n// stretch the appropriate element - 1 per input group\n\n.InputGroup_section--grow {\n\t.flex(1,1,auto);\n}\n\n\n// bring focused elements to the front so we can see their decoration\n\n.InputGroup_section > .FormInput,\n.InputGroup_section > .Button {\n\tposition: relative;\n\n\t&:focus {\n\t\tz-index: 1;\n\t}\n}\n\n\n\n\n//  Contiguous fields/buttons\n// ------------------------------\n\n.InputGroup--contiguous > .InputGroup_section {\n\t// pull subsequent children over their sibling's border\n\tmargin-left: -1px;\n\t&:first-child {\n\t\tmargin-left: 0;\n\t}\n\n\t// remove the border radius initially\n\t> .FormInput,\n\t> .Button,\n\t> .FormField .FormInput {\n\t\tborder-radius: 0;\n\t}\n\n\t// reapply where appropriate\n\t&:first-child > .FormInput,\n\t&:first-child > .Button,\n\t&:first-child > .FormField .FormInput {\n\t\t.border-left-radius(@border-radius-base);\n\t}\n\t&:last-child > .FormInput,\n\t&:last-child > .Button,\n\t&:last-child > .FormField .FormInput {\n\t\t.border-right-radius(@border-radius-base);\n\t}\n}\n","//\n// Modal\n// ==============================\n\n// Inspired by https://github.com/twbs/bootstrap\n\n\n\n\n// Wrapper\n// ------------------------------\n\n.Modal {\n\t.transition( visibility 140ms );\n\tbottom: 0;\n\tleft: 0;\n\toutline: 0;\n\toverflow-x: hidden;\n\toverflow-y: auto;\n\tposition: fixed;\n\tright: 0;\n\ttop: 0;\n\tvisibility: hidden;\n\tz-index: @zindex-modal;\n\t-webkit-overflow-scrolling: touch;\n\n\t&.is-open {\n\t\t.transition( none );\n\t\tvisibility: visible;\n\t}\n}\n\n\n\n\n// Dialog\n// ------------------------------\n\n\n\n// shell div to position the modal\n.Modal-dialog {\n\tmax-width: 100%;\n\tpadding: 10px;\n\tposition: relative;\n\twidth: auto;\n\tz-index: 2;\n}\n.Modal-dialog-enter {\n\t.animation-name( modalDialogEnter );\n\t.animation-duration( 260ms );\n\t.animation-timing-function( cubic-bezier(0.500, -0.550, 0.400, 1.550) );\n}\n.Modal-dialog-leave {\n\t.animation-duration( 140ms );\n\t.animation-name( modalDialogLeave );\n}\n\n\n// actual modal\n\n.Modal-content {\n\tbackground-color: @modal-bg;\n\tborder-radius: @border-radius-base;\n\tbox-shadow: 0 0 0 1px rgba(0,0,0,.175), 0 3px 8px rgba(0,0,0,.175);\n\toutline: none; // remove focus outline from opened modal\n\tposition: relative;\n}\n\n\n// sizes\n\n@media (min-width: @screen-sm) {\n\t.Modal-dialog {\n\t\tmargin: 80px auto;\n\t}\n\t.Modal-dialog--small  { width: @modal-width-small; }\n\t.Modal-dialog--medium { width: @modal-width-medium; }\n\t.Modal-dialog--large  { width: @modal-width-large; }\n}\n\n\n\n\n// Inner\n// ------------------------------\n\n// common\n.Modal__header,\n.Modal__body,\n.Modal__footer {\n\tmargin-left: @modal-padding-horizontal;\n\tmargin-right: @modal-padding-horizontal;\n\tpadding-bottom: @modal-padding-vertical;\n\tpadding-top: @modal-padding-vertical;\n\tposition: relative;\n}\n\n// header\n.Modal__header {\n\tbox-shadow: 0 2px 0 rgba(0,0,0,.05);\n\tz-index: 1;\n}\n.Modal__header__text {\n\tcolor: inherit;\n\tfont-size: 18px;\n\tfont-weight: 500;\n\tline-height: 1;\n\tmargin: 0;\n}\n\n// close button\n.Modal__header__close {\n\t.transition(opacity 140ms);\n\t// dechrome\n\tbackground: none;\n\tborder: none;\n\tcursor: pointer;\n\tline-height: 1ex; // get it? ;)\n\tmargin: 0;\n\n\t// style and position\n\topacity: .4;\n\tfont-size: 24px;\n\tpadding: @modal-padding-vertical @modal-padding-horizontal;\n\tposition: absolute;\n\tright: -@modal-padding-horizontal;\n\ttop: 0;\n\n\t// add the x\n\t&::after {\n\t\tcontent: \"\\00d7\";\n\t}\n\n\t&:hover,\n\t&:focus {\n\t\topacity: 1;\n\t\toutline: 0;\n\t}\n\t&:active {\n\t\tcolor: @app-danger;\n\t}\n}\n\n// body\n.Modal__body {\n\tmargin: 0;\n\t// max-height: @modal-body-height;\n\t// overflow: scroll;\n\tpadding: @modal-padding-vertical @modal-padding-horizontal;\n\t// -webkit-overflow-scrolling: touch;\n}\n\n// footer\n.Modal__footer {\n\tbox-shadow: 0 -2px 0 rgba(0,0,0,.05);\n\tz-index: 1;\n}\n\n\n\n\n// Background\n// ------------------------------\n\n.Modal-backdrop {\n\t.animation-duration( 140ms );\n\tbackground-color: @modal-backdrop-bg;\n\theight: 100%;\n\tleft: 0;\n\tposition: fixed;\n\ttop: 0;\n\twidth: 100%;\n\tz-index: @zindex-modal-backdrop;\n}\n.Modal-background-enter {\n\t.animation-name( modalBackdropEnter );\n}\n.Modal-background-leave {\n\t.animation-duration( 240ms );\n\t.animation-name( modalBackdropLeave );\n}\n","// \n// Pagination\n// ==============================\n\n\n\n\n// Base\n// ------------------------------\n\n.Pagination {\n\tcolor: @gray-light;\n\tdisplay: block;\n\tfont-size: @font-size-base;\n\tline-height: floor((@font-size-base * 2.2) + 2);\n\tmargin-bottom: 2em;\n}\n\n\n\n\n// Count\n// ------------------------------\n\n.Pagination__count {\n\tdisplay: inline-block;\n\tmargin-right: 1em;\n\tvertical-align: middle;\n}\n\n\n\n\n// List\n// ------------------------------\n\n.Pagination__list {\n\tdisplay: inline-block;\n\tvertical-align: middle;\n}\n\n\n\n\n// Item\n// ------------------------------\n\n.Pagination__list__item {\n\tbackground: none;\n\tborder: 1px solid transparent;\n\tborder-radius: 3px;\n\tcolor: @pagination-color;\n\tcursor: pointer;\n\tdisplay: inline-block;\n\tfloat: left; // Collapse white-space\n\tmargin-right: .25em;\n\tpadding: 0 .7em;\n\tposition: relative;\n\ttext-decoration: none;\n\t-webkit-appearance: none;\n\n\t\n\t// handle hover and focus\n\n\t&:hover,\n\t&:focus {\n\t\tbackground-color: @pagination-hover-bg;\n\t\tborder-color: @pagination-hover-border;\n\t\tcolor: @pagination-hover-color;\n\t\toutline: none;\n\t}\n\n\t\n\t// selected page\n\n\t&.is-selected {\n\t\t&,\n\t\t&:hover,\n\t\t&:focus {\n\t\t\tbackground-color: @pagination-active-bg;\n\t\t\tborder-color: @pagination-active-border;\n\t\t\tcolor: @pagination-active-color;\n\t\t\tcursor: default;\n\t\t\tz-index: 2;\n\t\t}\n\t}\n\t\n\t\n\t// disabled page\n\n\t&[disabled],\n\t&.is-disabled {\n\t\tbackground-color: @pagination-disabled-bg;\n\t\tborder-color: @pagination-disabled-bg;\n\t\tcolor: @pagination-disabled-color;\n\t\tcursor: default;\n\t}\n}","//\n// Pill\n// ==============================\n\n\n\n\n// base\n\n.Pill {\n\tdisplay: inline-block;\n\tfont-size: .85em;\n\tfont-weight: 500;\n\tmargin-right: .5em;\n\toverflow: hidden;\n\tline-height: 2.2em;\n}\n\n\n// common\n\n.Pill__label,\n.Pill__clear {\n\tbackground: none;\n\tborder: none;\n\tcursor: pointer;\n\tdisplay: block;\n\tfloat: left;\n\tpadding: 0 .9em;\n\t-webkit-appearance: none;\n\n\t// make pills - exaggerate the padding toward the radii so it looks even\n\t&:first-child {\n\t\t.border-left-radius(3em);\n\t\tpadding-left: 1.1em;\n\t}\n\t&:last-child {\n\t\t.border-right-radius(3em);\n\t\tpadding-right: 1.1em;\n\t}\n}\n\n\n// provide separation between the label and clear buttons\n// floating stops the margins from collapsing into eaching\n\n.Pill__label { margin-right: 1px; }\n.Pill__clear { margin-left: 1px; }\n\n\n\n\n// Variants\n// ------------------------------\n\n\n// standard\n\n.Pill--default > .Pill__label,\n.Pill--default > .Pill__clear {\n\t.pill-variant(@pill-default-color, mix(@pill-default-color, @body-bg, 10%));\n}\n.Pill--primary > .Pill__label,\n.Pill--primary > .Pill__clear {\n\t.pill-variant(@pill-primary-color, mix(@pill-primary-color, @body-bg, 10%));\n}\n.Pill--info > .Pill__label,\n.Pill--info > .Pill__clear {\n\t.pill-variant(@pill-info-color, mix(@pill-info-color, @body-bg, 10%));\n}\n.Pill--success > .Pill__label,\n.Pill--success > .Pill__clear {\n\t.pill-variant(@pill-success-color, mix(@pill-success-color, @body-bg, 10%));\n}\n.Pill--warning > .Pill__label,\n.Pill--warning > .Pill__clear {\n\t.pill-variant(@pill-warning-color, mix(@pill-warning-color, @body-bg, 10%));\n}\n.Pill--danger > .Pill__label,\n.Pill--danger > .Pill__clear {\n\t.pill-variant(@pill-danger-color, mix(@pill-danger-color, @body-bg, 10%));\n}\n\n\n// inverted\n\n.Pill--default-inverted > .Pill__label,\n.Pill--default-inverted > .Pill__clear {\n\t.pill-inverted-variant(white, @pill-default-color);\n}\n.Pill--primary-inverted > .Pill__label,\n.Pill--primary-inverted > .Pill__clear {\n\t.pill-inverted-variant(white, @pill-primary-color);\n}\n.Pill--info-inverted > .Pill__label,\n.Pill--info-inverted > .Pill__clear {\n\t.pill-inverted-variant(white, @pill-info-color);\n}\n.Pill--success-inverted > .Pill__label,\n.Pill--success-inverted > .Pill__clear {\n\t.pill-inverted-variant(white, @pill-success-color);\n}\n.Pill--warning-inverted > .Pill__label,\n.Pill--warning-inverted > .Pill__clear {\n\t.pill-inverted-variant(white, @pill-warning-color);\n}\n.Pill--danger-inverted > .Pill__label,\n.Pill--danger-inverted > .Pill__clear {\n\t.pill-inverted-variant(white, @pill-danger-color);\n}\n","// \n// Tags\n// ==============================\n\n.pill-variant(@color, @background) {\n\tbackground-color: @background;\n\tcolor: @color;\n\n\t// states\n\t&:hover,\n\t&:focus {\n\t\tbackground-color: darken(@background, 4%);\n\t\toutline: none;\n\t}\n\t&:active {\n\t\tbackground-color: darken(@background, 8%);\n\t}\n}\n\n.pill-inverted-variant(@color, @background) {\n\tbackground-color: @background;\n\tcolor: @color;\n\n\t// states\n\t&:hover,\n\t&:focus {\n\t\tbackground-color: lighten(@background, 5%);\n\t\toutline: none;\n\t}\n\t&:active {\n\t\tbackground-color: darken(@background, 5%);\n\t}\n}","// \n// Segmented Control\n// ==============================\n\n\n// base\n\n.SegmentedControl {\n\tborder: 1px solid @input-border-color;\n\tborder-radius: 5px;\n\tdisplay: table;\n\tfont-size: @font-size-sm;\n\twidth: 100%;\n}\n\n// with equal widths\n\n.SegmentedControl--equal-widths {\n\ttable-layout: fixed;\n}\n\n\n// item\n\n.SegmentedControl__item {\n\tdisplay: table-cell;\n\tpadding: 2px 1px;\n\n\t&:first-child {\n\t\tpadding-left: 2px;\n\t}\n\t&:last-child {\n\t\tpadding-right: 2px;\n\t}\n}\n\n\n// item\n\n/*button*/.SegmentedControl__button {\n\t.text-overflow();\n\tbackground: none;\n\tborder: none;\n\tborder-radius: 3px;\n\tdisplay: block;\n\n\t// buttons receive padding from the browser. we want to\n\t// squeeze all the space that we can out of this small component\n\tpadding-left: 0;\n\tpadding-right: 0;\n\n\twidth: 100%;\n\t-webkit-appearance: none;\n\n\t&:hover,\n\t&:focus {\n\t\tbackground-color: rgba(0, 0, 0, 0.05);\n\t\toutline: none;\n\t}\n}\n\n// variants\n\n.control-variant(@color) {\n\tcolor: @color;\n\n\t&.is-selected {\n\t\tbackground-color: @color;\n\t\tcolor: white;\n\t}\n}\n\n.SegmentedControl--default .SegmentedControl__button{ .control-variant(@text-color); }\n.SegmentedControl--muted   .SegmentedControl__button{ .control-variant(@gray); }\n\n.SegmentedControl--danger  .SegmentedControl__button{ .control-variant(@app-danger); }\n.SegmentedControl--info    .SegmentedControl__button{ .control-variant(@app-info); }\n.SegmentedControl--primary .SegmentedControl__button{ .control-variant(@app-primary); }\n.SegmentedControl--success .SegmentedControl__button{ .control-variant(@app-success); }\n.SegmentedControl--warning .SegmentedControl__button{ .control-variant(@app-warning); }\n\n\n\n\n","//\n// Spinner\n// ==============================\n\n\n\n\n\n// Base\n// ------------------------------\n\n.Spinner {\n\tdisplay: inline-block;\n\tfont-size: @spinner-dot-size;\n\theight: @spinner-dot-size;\n\tposition: relative;\n\ttext-align: center;\n\tvertical-align: middle;\n}\n\n\n\n\n\n// Dots\n// ------------------------------\n\n.Spinner_dot {\n\t.animation( pulse 1s infinite ease-in-out );\n\t.square(1em);\n\tborder-radius: 50%;\n\tdisplay: inline-block;\n\tvertical-align: top;\n}\n&.Spinner_dot--second {\n\t.animation-delay( 160ms );\n\tmargin-left: 1em;\n}\n&.Spinner_dot--third {\n\t.animation-delay( 320ms );\n\tmargin-left: 1em;\n}\n\n\n\n\n// Types\n// ------------------------------\n\n\n// default\n.Spinner--default > .Spinner_dot { background-color: @spinner-dot-bg; }\n\n// primary\n.Spinner--primary > .Spinner_dot { background-color: @app-primary; }\n\n// inverted\n.Spinner--inverted > .Spinner_dot { background-color: white; }\n\n\n\n\n// Types\n// ------------------------------\n\n\n// default\n.Spinner--default > .Spinner_dot { background-color: @spinner-dot-bg; }\n\n// primary\n.Spinner--primary > .Spinner_dot { background-color: @app-primary; }\n\n// inverted\n.Spinner--inverted > .Spinner_dot { background-color: white; }\n\n\n\n\n// Sizes\n// ------------------------------\n\n\n// small\n.Spinner--sm {\n\tfont-size: @spinner-dot-size-sm;\n\theight: @spinner-dot-size-sm;\n}\n\n// large\n.Spinner--lg {\n\tfont-size: @spinner-dot-size-lg;\n\theight: @spinner-dot-size-lg;\n}\n\n\n\n\n\n// Inside Buttons\n// ------------------------------\n\n.Button > .Spinner {\n\t.Spinner--sm();\n\tmargin-right: 2em;\n\n\t&:only-child {\n\t\tmargin-right: 0;\n\t}\n}\n\n\n\n\n\n\n\n\n\n","// \n// Position\n// ==============================\n\n\n// Center an element\n\n.center-block {\n\tmargin: 0 auto;\n}","/**\n * Vertical alignment utilities\n * Depends on an appropriate `display` value.\n */\n\n.u-align-baseline {\n\tvertical-align: baseline !important;\n}\n\n.u-align-bottom {\n\tvertical-align: bottom !important;\n}\n\n.u-align-middle {\n\tvertical-align: middle !important;\n}\n\n.u-align-top {\n\tvertical-align: top !important;\n}","// \n// Clearfix\n// ==============================\n\n// For modern browsers\n// 1. The space content is one way to avoid an Opera bug when the\n//    contenteditable attribute is included anywhere else in the document.\n//    Otherwise it causes space to appear at the top and bottom of elements\n//    that are clearfixed.\n// 2. The use of `table` rather than `block` is only necessary if using\n//    `:before` to contain the top-margins of child elements.\n//\n// Source: http://nicolasgallagher.com/micro-clearfix-hack/\n\n.clearfix() {\n\t&:before,\n\t&:after {\n\t\tcontent: \" \"; // 1\n\t\tdisplay: table; // 2\n\t}\n\t&:after {\n\t\tclear: both;\n\t}\n}","/**\n * Contain floats\n * see ../mixins/clearfix for more information\n */\n\n.u-clearfix {\n\t.clearfix()\n}\n\n/**\n * Floats\n */\n\n.u-float-left {\n\tfloat: left !important;\n}\n\n.u-float-right {\n\tfloat: right !important;\n}","/**\n * Pins to all corners by default. But when a width and/or height are\n * provided, the element will be centered in its nearest relatively-positioned\n * ancestor.\n */\n\n.u-pos-absolute-center {\n\tbottom: 0 !important;\n\tleft: 0 !important;\n\tmargin: auto !important;\n\tposition: absolute !important;\n\tright: 0 !important;\n\ttop: 0 !important;\n}\n\n/**\n * 1. Make sure fixed elements are promoted into a new layer, for performance\n *    reasons.\n */\n\n.u-pos-fixed {\n\tposition: fixed !important;\n\tbackface-visibility: hidden; /* 1 */\n}\n\n.u-pos-absolute {\n\tposition: absolute !important;\n}\n\n.u-pos-relative {\n\tposition: relative !important;\n}\n\n.u-pos-static {\n\tposition: static !important;\n}"],"sourceRoot":""}]);
